import { useState } from 'react';
import { Navbar, Nav, NavDropdown,Container,Row, Col, Button } from 'react-bootstrap';
import { Route, useHistory, Switch, Link} from 'react-router-dom';
import axios from 'axios';
import data from './data.js'
import Detail from './Detail.js'
import './App.css';


function App() {
  let [shoes,setShoes] = useState(data);
  let history = useHistory(); // 페이지 이동 기록 저장

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="/">ShoeShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />      
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href="/">Home</Nav.Link>
            <Nav.Link href="/de">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Route exact path="/">
        <div className = "jumbo" style={{height: "280px"}}>
          <h2>아무거나</h2>
          <button> 버튼 </button>
        </div>

        <Container>
          <Row>
            {
              shoes.map((shoe, i)=>{
                return <Col className="col-md-4"><Shoe shoe = {shoe} i={i}/></Col>
              })
            }
          </Row>
        </Container>

        <Button variant="primary" onClick={()=>{
          axios.get("https://codingapple1.github.io/shop/data2.json")
          .then((res)=>{
            let temp = [...shoes,...res.data];
            setShoes(temp);
          })
          .catch(console.log("실패"))
        }}>더보기</Button>{' '}
      </Route>

      <Route path='/detail/:id'>
        <Detail shoes= {shoes}/>
      </Route>
    </div>
  );
}

function Shoe(props){
  return(
    <Link to ={"./detail/"+props.i}>
      <div className="shoe">
        <img src={"https://codingapple1.github.io/shop/shoes" + (props.i + 1) + ".jpg"} width="100%" />
        <h4>{props.shoe.title}</h4>
        <p>{props.shoe.content}</p>
        <p>{props.shoe.price}</p>
      </div>
    </Link>
  )
}

export default App;
